const date1 = new Date();

const Time =(num:number)=> {
  const date1 = new Date();
  let hour:number = date1.getHours() 
  date1.getHours() 
  if (num == 0){
    if (date1.getMinutes()>29){
      return(`${hour}`+':30')
    }else{
      return(`${hour}`+':00')
    }
  }else if(num == 1){
    if (date1.getMinutes()>29){
      return(`${hour+1}`+':00')
    }else{
      return(`${hour}`+':30')
    }
  }else if(num == 2){
    if (date1.getMinutes()>29){
      return(`${hour+1}`+':30')
    }else{
      return(`${hour+1}`+':00')
    }
  }else if(num == 3){
    if (date1.getMinutes()>29){
      return(`${hour+2}`+':00')
    }else{
      return(`${hour+2}`+':30')
    }
  }else if(num == 4){
    if (date1.getMinutes()>29){
      return(`${hour+2}`+':30')
    }else{
      return(`${hour+3}`+':00')
    }
  }
}

